/* Page Loader */
.js-loader {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.js-loader.is-loaded {
  position: unset;
}

.js-loader::before {
  content: "";
  position: fixed;
  z-index: 110;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  text-align: center;
  background: #707c8c;
  pointer-events: none;
}

.js-loader::after {
  content: "welcome";
  position: fixed;
  z-index: 110;
  top: 50%;
  left: 50%;
  width: 50px;
  height: 50px;
  border-radius: 50px;
  margin: -25px 0 0 -25px;
  pointer-events: none;
  color: #fff;
}

.js-loader.is-loaded::before {
  opacity: 0;
  transition: all 0.5s ease-in;
  z-index: -1;
}

.js-loader.is-loaded::after {
  opacity: 0;
  transition: all 0.3s ease-in;
  z-index: -1;
}

section {
  height: 100vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: aquamarine;
}

.js-fadein {
  display: inline-block;
  margin: 0 20px;
  height: 60px;
  width: 60px;
  background: #888;
  opacity: 0;
}